.btn {
  padding: 7px 20px;
  cursor: pointer;
  border-bottom: 1px solid #3f51b5;
  font-size: 1rem;
  text-align: center;

  &:hover,
  &--active {
    background: #3f51b5;
    color: #fff;
  }

  &--primary {
    background: $primary-color;
    color: #fff;
    border: none;
    &:hover {
      background: rgba($color: $primary-color, $alpha: .9);
    }
  }

  &--danger {
    background: $red;
    color: #fff;
    border: none;
    &:hover {
      background: rgba($color: $red, $alpha: .9);
    }
  }
}