    background-color: #3f51b5;
    -webkit-transition: 0.4s;
    transition: 0.4s; }
  .select__input.has--content ~ .focus-border,
  .select__input:focus ~ .focus-border,
  .input__field_input.has--content ~ .focus-border,
  .input__field_input:focus ~ .focus-border {
    width: 100%;
    -webkit-transition: 0.4s;
    transition: 0.4s; }
  .select__input ~ label,
  .input__field_input ~ label {
    position: absolute;
    left: 0;
    width: 100%;
    top: 6px;
    color: #aaa;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    z-index: 0;
    letter-spacing: 0.5px; }
  .select__input.has--content ~ label,
  .select__input:focus ~ label,
  .input__field_input.has--content ~ label,
  .input__field_input:focus ~ label {
